<template>
	<auth-template title="Recuperar senha">
    <form @submit.prevent="cadastrar">
      <v-text-field
        id="email"
        v-model="email"
        label="Informe seu e-mail"
        :rules="emailRules"
        v-on:keyup.enter="enterClick" 
      />
     
      <div class="text-center mt-4">
        <btn-confirm
          text="Enviar"
          :disabled="btnDisabled"
          :loading="progressLoader"
          @clicked="onRecoveryPassword"
        />
      </div>

      <div class="text-center mt-4">
				<btn-text
					text="Voltar"
          @clicked="goToBack()"
        />
      </div>
    </form>
  </auth-template>
</template>

<script>
import AuthTemplate from './AuthTemplate'
import BtnConfirm from '@/components/globais/buttons/confirm/BtnConfirm'
import BtnText from '@/components/globais/buttons/text/BtnText'

import ValidationMixin from '@/mixins/ValidationMixin'
import NavigationMixin from '@/mixins/NavigationMixin'

export default {
  name: 'RecoveryPassword',
  components: { AuthTemplate, BtnConfirm, BtnText },
  mixins: [ NavigationMixin, ValidationMixin ],
  props: {
    progressLoader: { default: false }
  },
  data: () => ({
    email: ''  
  }),
  computed: {
    btnDisabled () {
      if (!this.isEmailValid(this.email)) return true
      return false
    }
  },
  mounted () {
    document.querySelector('#email').focus()
  },
  methods: {
    enterClick () {
      if (!this.btnDisabled) this.enviar()
      else this.showSnackbarErro('Informe seu e-mail!')
    },
    onRecoveryPassword () {
      this.$emit('onRecoveryPassword', { email: this.email })
    }
  }
}
</script>